import React from 'react' 
import s from './Header.module.scss'
import cart from '../../assets/cart.png'
import logo from '../../assets/Logo.png'
import menu from '../../assets/menu.png'
import {Link} from 'react-router-dom'
import { useSelector } from 'react-redux'
const Header = () => {
  
  let count = useSelector(state => state.cart.cart.reduce((sum,elem) => {
    return sum + elem.count
  },0))
  return (
    <>
    <div className={s.burgerMenu}>
    <Link to='/'>home</Link>
       <Link to='/about'>about me</Link>
       <Link to='/shop'>shop</Link>
       <Link to='/brands'>brands</Link>
       <Link to='/contact'>contact</Link></div>
    <header className={s.header}>
    
   <section className={s.flex}>
      <div className={s.logo}><Link to='/'><img src={logo}></img></Link></div>
      <nav className={s.navbar}>
         <Link to='/'>home</Link>
         <Link to='/about'>about me</Link>
         <Link to='/shop'>shop</Link>
         <Link to='/brands'>brands</Link>
         <Link to='/contact'>contact</Link>
      </nav>
        <div className={s.icons}>
          <span>{count}</span>
          <Link to='/cart' className={s.cart}><img src={cart}></img></Link>
          <div className={s.menu}>
            <img src={menu}></img>
            </div>
        </div>
   </section>
</header>
</>
  )
}

export default Header