import React from 'react'
import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import { brandDetailApiThunk, removeBrandOrPerfume } from '../../../redux/perfumeSlice'
const BrandPage = () => {
    let {id} = useParams()
    let brandDetail = useSelector(state => state.allPerfumes.brandDetail)
    let dispatch = useDispatch();
  useEffect(() => {
    dispatch(brandDetailApiThunk(id))
  
    return () => {
      dispatch(removeBrandOrPerfume())
    }
  }, [id])
 
    return (
    <div>{Object.keys(brandDetail).length === 0 ? <div>LOADING</div> : <div><img src={brandDetail[0].imageUrl}></img></div> }</div>
  )
}

export default BrandPage